import React, { useMemo } from 'react';
import { Inline } from '@ali/4ever-cangjie';
import { GroupChatNormal } from '@ali/we-design';
import { convertMarks } from '@ali/4ever-bamboo';
import styled from 'styled-components';

const NORMAL_TEXT_COLOR = 'rgba(49, 126, 208, 1)';
const HOVER_BG_COLOR = 'rgba(126,134,142,0.12)';

const Wrapper = styled.span<{ style: React.CSSProperties }>`
  color: ${NORMAL_TEXT_COLOR};
  min-height: 23px;
  border-radius: 4px;
  padding: 3px;
  transition: all 0.2s;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 11pt;
  font-weight: normal;
  cursor: text;
  line-height: 1.5;
  width: 100%;

  &:hover {
    background-color: ${HOVER_BG_COLOR};
  }

  .weicon {
    display: inline-flex;
    font-size: 120%;
    line-height: 1em;
  }

  svg {
    transform: translateY(3px);
    fill: currentColor;
    margin-right: 5px;
  }
`;

const GroupTag = (props: { node: Inline }) => {
  const { node } = props;
  const title = node.data.metadata.imTitle;
  const style = useMemo(() => convertMarks(node.getFirstText()?.marks || []), [node]);

  return (
    <Wrapper style={style}>
      <GroupChatNormal />
      {title}
    </Wrapper>
  );
};

export default GroupTag;
